.App {
  font-size: large;
}

.userCard {
  color: rgb(35, 37, 37);
  display: flex;
  justify-content: space-around;
  margin-top: 5px;
  background-color: rgb(208, 163, 230);
  border-radius: 3px;
  align-items: baseline;
  box-shadow: 8px 12px 18px rgb(104, 77, 77);
}

.userCard h3 {
  font-weight: bold;
}
.userCard p {
  font-weight: lighter;
}
.userCard button {
  font-weight: lighter;

  color: black;
}
